import {
  CButton,
  CCard,
  CCardBody,
  CCardGroup,
  CCol,
  CRow,
  CFormLabel,
  CModal,
  CModalHeader,
  CModalFooter,
  CModalTitle,
  CModalBody,
  CTooltip,
  CTable,
  CTableHead,
  CTableRow,
  CTableBody,
  CTableDataCell,
  CTableHeaderCell,
} from "@coreui/react-pro";
import React, { useEffect, useState } from "react";
import NavBar from "../../navbars/ev_super_admin/nav_normal_school";
import {
  faCog,
  faTimesCircle,
  faSave,
} from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { Formik, Form } from "formik";
import * as Yup from "yup";
import Loader from "src/loader/Loader";
import EVFormikControl from "src/ev_formik_controls/EVFormikControl";
import EDU_AXIOS from "src/helpers/edu_axios";
import notify from "src/helpers/notify";

const class_mapping = (props) => {
  let school_id = props.match.params.school_id;
  let [visible, setVisible] = useState(false);
  let [teacherList, setTeacherList] = useState([]);
  let [teacherName, setTeacherName] = useState();
  let [teacherDList, setTeacherDList] = useState([]);
  let [visibleDetail, setVisibleDetail] = useState(false);
  let [classNames, setClassName] = useState();
  let [subjectName, setSubjectName] = useState();
  let [sections, setSections] = useState([]);
  let [teacher_id, setTeacherId] = useState();
  useEffect(() => {
    EDU_AXIOS.get(
      `${process.env.REACT_APP_API_PATH}/tm/get-teachers-by-school/${school_id}`
    )
      .then((response) => {
        setTeacherList(response.data.data);
      })
      .catch((error) => {
        console.log(error);
      });
  }, []);

  let Record = () => {
    let el = [];
    el.push(
      <CCol md={12}>
        <h5>Teachers</h5>
      </CCol>
    );
    if (!!teacherList && teacherList.length > 0) {
      for (let i = 0; i < teacherList.length; i++) {
        let row = [];
        row.push(
          <CCol md={12} key={teacherList[i].teacher_id}>
            <div class="d-grid gap-2">
              <CTooltip content="View Teacher">
                <CButton
                  color="light"
                  className="mb-2"
                  onClick={() => {
                    getDetail(teacherList[i].name, teacherList[i].teacher_id);
                  }}
                >
                  {teacherList[i].name}
                </CButton>
              </CTooltip>
            </div>
          </CCol>
        );
        el.push(row);
      }
    } else {
      el.push(
        <CCol md={12} style={{ textAlign: "center" }}>
          No record found...
        </CCol>
      );
    }
    return el;
  };

  // get sections by class
  let getDetail = (name, teacherId) => {
    setTeacherName(name);
    setTeacherId(teacherId);
    console.log("test");
    EDU_AXIOS.get(
      `${process.env.REACT_APP_API_PATH}/cm/get-mapping/${teacherId}`
    )
      .then((response) => {
        console.log(response.data.data[0][0].class_name)
        setTeacherDList(response.data.data);
        setVisibleDetail(true);
      })
      .catch((error) => {
        setTeacherDList([]);
        setVisibleDetail(false);
        console.log(error);
      });
  };

  
  // show sections
  let showDetail = () => {
    console.log("show details")
    let record = [];
    record.push(
      <CRow>
        <CCol
          md={12}
          style={{
            height: "50px",
            paddingTop: "10px",
            paddingBottom: "10px",
            backgroundColor: "#EBEDEF",
          }}
        >
          <b> {teacherName} </b>
        </CCol>
      </CRow>
    );
    if (teacherDList.length != []) {
      record.push(
        <CRow className="mt-2">
          <CCol md={12}>
            <CTable striped>
              <CTableHead color="dark">
                <CTableRow>
                  <CTableHeaderCell scope="col">Subject</CTableHeaderCell>
                  <CTableHeaderCell scope="col">Class</CTableHeaderCell>
                  <CTableHeaderCell scope="col">Section</CTableHeaderCell>
                  <CTableHeaderCell scope="col">Action</CTableHeaderCell>
                </CTableRow>
              </CTableHead>
              <CTableBody>
                {teacherDList.map((t) => {
                  return t.map((s) => {
                    return (
                      <CTableRow key={s.subject_name + "_" + s.class_name}>
                        <CTableDataCell>{s.subject_name}</CTableDataCell>
                        <CTableDataCell>{s.class_name}</CTableDataCell>
                        <CTableDataCell>
                          {/* {s.sections.toString().split().join(",")} */}
                          <div> {
                            s.sections.map((items)=>{
                              return <>
                              
                                <span> <input type="checkbox" value={items} onChange={(event)=>setSectionss(event.target.value)}></input> {items}</span>
                                </>
                            })
                          }
                           </div>
                        </CTableDataCell>
                        <CTableDataCell>
                          <CTooltip content="manage">
                            <CButton
                              color="light"
                              onClick={() => {
                                showManageModal(s.subject_name, s.class_name);
                              }}
                            >
                              <FontAwesomeIcon icon={faCog} />
                            </CButton>
                          </CTooltip>
                        </CTableDataCell>
                      </CTableRow>
                    );
                  });
                })}
              </CTableBody>
            </CTable>
          </CCol>
        </CRow>
      );
    } else {
      <CRow className="mt-2">
        <CCol md={12} style={{ textAlign: "center" }}>
          No record found
        </CCol>
      </CRow>;
    }

    return record;
  };
  // manage mapping

  const setSectionss=(val)=>{
    alert(val);
  }

  let showManageModal = (subjectName, className) => {
    EDU_AXIOS.get(
      `${process.env.REACT_APP_API_PATH}/cm/get-sections-by-class/${school_id}/${className}`
    )
      .then((response) => {
        setSubjectName(subjectName);
        setClassName(className);
        setVisible(true);
        let data = response.data.data.map((d) => {
          return {
            text: d.section_name,
            value: d.section_name,
          };
        });
        setSections(data);
      })
      .catch((error) => {
        setVisible(false);
        console.log(error);
      });
  };
  // formik params
  const initialValues = {
    c_subject: subjectName,
    c_class_name: classNames,
    c_sections: [],
  };

  // validation rules of the form fields
  const validationSchema = Yup.object().shape({
    c_subject: Yup.string().required("Subject is required"),
    c_class_name: Yup.string().required("Class name is required"),
    sections: Yup.array().min(1).of(Yup.string().trim().required()),
  });
  const onSubmit = async (values, { resetForm }) => {
    await EDU_AXIOS.post(process.env.REACT_APP_API_PATH + "/cm/class-mapping", {
      school_id: school_id,
      teacher_id: teacher_id,
      c_class: classNames,
      c_subject: subjectName,
      sections: values.sections,
    })
      .then(async (res) => {
        resetForm({ values: "" });
        notify("success", res.data.message);
        setVisible(false);
        getDetail(classNames, teacher_id);
      })
      .catch((err) => {
        notify("error", err);
      });
  };
  return (
    <CCardGroup>
      <CCard className="p-2">
        <CCardBody>
          <CRow>
            <CCol>
              <NavBar id={school_id} />
            </CCol>
          </CRow>
          <CRow>
            <CCol md="8" lg="6" xl="8">
              <h5>Class Mapping</h5>
            </CCol>
          </CRow>
          <p className="text-muted">Teacher class mapping</p>
          <CRow>
            <CCol md={2}>{Record()}</CCol>
            <CCol md={10}>{visibleDetail && showDetail()}</CCol>
          </CRow>
          {/*Manage Section Modal box*/}
          <CModal
            size="lg"
            visible={visible}
            onDismiss={() => setVisible(false)}
          >
            <CModalHeader onDismiss={() => setVisible(false)}>
              <CModalTitle>Class Mapping [ {teacherName} ]</CModalTitle>
            </CModalHeader>
            <Formik
              initialValues={initialValues}
              validationSchema={validationSchema}
              onSubmit={onSubmit}
            >
              {(formik) => (
                <Form className="row g-3">
                  <CModalBody>
                    <CRow>
                      <CCol md={6}>
                        <CFormLabel
                          htmlFor="subject"
                          className="required-field"
                        >
                          Subject
                        </CFormLabel>
                        <EVFormikControl
                          control="input"
                          type="text"
                          name="c_subject"
                          id="c_subject"
                          value={formik.values.c_subject}
                          disabled={true}
                          placeholder="Subject"
                        />
                      </CCol>
                      <CCol md={6}>
                        <CFormLabel
                          htmlFor="class_name"
                          className="required-field"
                        >
                          Class
                        </CFormLabel>
                        <EVFormikControl
                          control="input"
                          type="text"
                          name="c_class_name"
                          id="c_class_name"
                          value={formik.values.c_class_name}
                          disabled={true}
                          placeholder="Class"
                        />
                      </CCol>
                    </CRow>
                    <CRow className="mt-2">
                      <CCol md={12}>
                        <CFormLabel
                          htmlFor="c_sections"
                          className="required-field"
                        >
                          Sections
                        </CFormLabel>
                        <br />
                        <EVFormikControl
                          control="checkbox"
                          name="sections"
                          id="sections"
                          options={sections}
                          inline
                        />
                      </CCol>
                    </CRow>
                  </CModalBody>
                  <CModalFooter>
                    <CButton
                      block
                      color="light"
                      onClick={() => setVisible(false)}
                    >
                      <FontAwesomeIcon icon={faTimesCircle} /> Cancel
                    </CButton>
                    <CButton
                      block
                      color="dark"
                      type="submit"
                      disabled={
                        formik.isSubmitting || !formik.dirty || !formik.isValid
                      }
                      style={{ marginLeft: "15px" }}
                    >
                      {formik.isSubmitting ? (
                        <Loader />
                      ) : (
                        <>
                          <FontAwesomeIcon icon={faSave} /> Save
                        </>
                      )}
                    </CButton>
                  </CModalFooter>
                </Form>
              )}
            </Formik>
          </CModal>

          {/*End Manage section modal box*/}
        </CCardBody>
      </CCard>
    </CCardGroup>
  );
};

export default class_mapping;
